<style lang="scss" scoped>
.card{
    color: white;

    .content{
        display: flex;
        text-align: center;
        justify-content: center;
        :first-child{
            margin-right: 40px;
            color: red;
            text-shadow: 2px 2px 0  white;
        }
    }
}
</style>
<template>
    <div class="card">
        <h1>{{idea.activity}}</h1>
        <div class="content">
            <p>{{accessibility}}</p>
            <p>{{idea.type}}</p>
        </div>
    </div>
</template>
<script>
export default {
    props: ['idea'],
    computed: {
        accessibility(){
            if(this.idea.accessibility <= 0.3) return 'easy';
            else if (this.idea.accessibility <= 0.6) return 'medium';
            else return 'difficult';
        }
    }
    
}
</script>